.intro {
  display: block;
  color: #fff;
  font-size: 2rem;
  font-family: Roboto, sans-serif;
  font-weight: 100;
  /* line-height: 45px; */
}
.intro span p {
  width: fit-content;
}
.intro-wrapper {
  background: url("./assets/react-cover2.jpeg") top center no-repeat scroll #111;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  height: 99vh;
}
.intro-message {
  width: 21.5ch;
  white-space: nowrap;
  overflow: hidden;
  border-right: 4px solid #fff;
  animation: cursor 1s step-start infinite, text 5s steps(18) alternate infinite;
}
.about{
  display: flex;
}
.about-under-work {
  font-style: italic;
  font-size: '20px';
  animation: ani 10s infinite;
}

@keyframes ani{
  from{
      letter-spacing: 35px;
      filter: blur(5px);
      opacity: 0;
  }to{
      letter-spacing: 0;
      filter: blur(0);
      opacity: 1px;
  }
}

@keyframes cursor {
  0%,
  100% {
    border-color: #fff;
  }
}

@keyframes text {
  0% {
    width: 0;
  }
  100% {
    width: 20ch;
  }
}
